.header {
  margin-top: 60px;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Lora", serif;
  color: #444;
}

.headerTitleSm {
  position: absolute;
  top: 18%;
  font-size: 20px;
}

.headerTitleLg {
  position: absolute;
  top: 20%;
  font-size: 100px;
}

.headerImg {
  width: 100%;
  height: 450px;
  margin-top: 80px;
  object-fit: cover;
}

@media only screen and (max-width: 500px) {
  .headerTitleSm {
    font-size: 12px;
    top: 9%;
  }
  
  .headerTitleLg {
    top: 11%;
    font-size: 30px;
    color: rgb(199, 199, 199);
  }

  .headerImg {
    width: 100%;
    height: 250px;
    margin-top: 0px;
  }
}
