.sidebar {
  flex: 3;
  height: fit-content;
  margin: 20px;
  padding-bottom: 30px;
  background-color: #fdfbfb;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebarItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebarTitle {
  margin: 10px;
  padding: 5px;
  width: 80%;
  border-top: solid 1px #a7a4a4;
  border-bottom: solid 1px #a7a4a4;
  text-align: center;
  font-family: "Varela Round", sans-serif;
  font-size: 12px;
  line-height: 19px;
  color: #222222;
  font-weight: 600;
}

.sidebarItem > img {
  margin-top: 15px;
  width: 250px;
  height: 250px;
}

.sidebarItem > p {
  padding: 30px;
}

.sidebarList {
  list-style-type: none;
  margin-bottom: 30px;
}

.sidebarListItem {
  display: inline-block;
  width: 50%;
  margin-top: 15px;
  cursor: pointer;
}

.sidebarSocial {
  margin-top: 15px;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sidebarIcon {
  font-size: 16px;
  margin-left: 10px;
}


@media only screen and (max-width: 500px){
  .sidebar {
    width: 100%;
    margin: 10px 0;
    padding: 0;
  }

  .sidebarTitle {
    margin: 10px 0;
    padding: 2px 0;
    font-size: 10px;
  }

  .sidebarItem > img {
    width: 60%;
  }
  
  .sidebarItem > p {
    margin: 20px;
    text-align: justify;
    font-size: 14px;
  }
  
  .sidebarList {
    text-align: center;
    margin: 10px 0;
  }
  
  .sidebarListItem {
    display: inline-flex;
    width: 59%;
    margin-top: 10px;
    margin-left: -15px;
    cursor: pointer;
  }
  
  .sidebarSocial {
    margin-top: 15px;
    margin-bottom: 50px;
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .sidebarIcon {
    font-size: 16px;
    margin-left: 10px;
  }
}